import { useEffect, useState } from "react";
import Shimmer from './Shimmer';
import MenuCotegory from "./Menucotegory";
import RestaurantDetail from "./RestaurantDetail";


const RestaurantsMenu = () => {
    const [resInfo, setresInfo] = useState(null);

 useEffect(()=>{
    fetchData();
}, []);

async function fetchData() {
    const data = await fetch("https://www.swiggy.com/dapi/menu/pl?page-type=REGULAR_MENU&complete-menu=true&lat=12.9351929&lng=77.62448069999999&restaurantId=70225&catalog_qa=undefined&submitAction=ENTER");
    const json = await data.json();
    setresInfo( json?.data);
    
}


if(resInfo === null){
     return <Shimmer/>
}

const resItems = resInfo?.cards[4]?.groupedCard?.cardGroupMap?.REGULAR?.cards;
resItems.shift();
resItems.pop();
resItems.pop();
// console.log(resItems);


    return(
        <div className="restaurants">
            <div className="restaurantMenu">
                <RestaurantDetail resDetail={resInfo?.cards[2].card.card.info} />
                <div className="res-menu">
                    {resItems?.map((items, index)=>{
                        return < MenuCotegory key={index} resData={items} />
                    })}
                    
                </div>

            </div>
        </div>
    )
};
export default RestaurantsMenu;